@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);

// Variables
$dark-blue: #1e1b2d;
$light-blue: #828092;
$white: #fff;
$black: #000;
$open-sans: "Open Sans", sans-serif;

// clear-fix mixin
@mixin cf {
  &::before,
  &::after {
    content: "";
    display: table;
  }
  &::after {
    clear: both;
  }
}

a {
  text-decoration: none;
}

h1 {
  font-family: $open-sans;
  font-weight: 600;
}

// Base styles
.card {
  float: left;
  padding: 1rem;
  aspect-ratio: 1/1.618;
  min-width: 500px;
  .menu-content {
    @include cf;
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      display: inline-block;
    }
    a {
      color: $white;
    }
  }
  .wrapper {
    background-color: $white;
    border-radius: 32px;
    min-height: 540px;
    position: relative;
    overflow: hidden;
    filter: drop-shadow(0 20px 13px rgb(0 0 0 / 0.03))
      drop-shadow(0 8px 5px rgb(0 0 0 / 0.08));
    &:hover {
      .data {
        transform: translateY(0);
        background-color: #000000b2;
      }
    }
  }
  .data {
    position: absolute;
    bottom: 0;
    width: 100%;
    transform: translateY(calc(120px + 1em));
    transition: all 0.3s;
    .content {
      padding-top: 0.3em;
      padding-right: 1.5em;
      padding-bottom: 1.5em;
      padding-left: 1.5em;
      position: relative;
      z-index: 1;
      background-color: rgba(0, 0, 0, 0.726);
      color: white;
      box-shadow: 0 5px 30px 10px rgba($black, 0.3);
      border-radius: 0, 32px;
    }
  }
  .type {
    font-size: 12px;
  }
  .title {
    margin-top: 5px;
    margin-bottom: 10px;
  }
  .text {
    height: 70px;
    margin: 0;
  }
  input[type="checkbox"] {
    display: none;
  }
  input[type="checkbox"]:checked + .menu-content {
    transform: translateY(-60px);
  }
}

.project-card {
  .date {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #333;
    color: $white;
    padding: 0.7em 2.5em 1.5rem 0.7em;
    border-radius: 0 0 90% 0;
    span {
      display: block;
      text-align: center;
    }
    .day {
      font-weight: 700;
      font-size: 24px;
      text-shadow: 2px 3px 2px rgba($black, 0.18);
    }
    .month {
      text-transform: uppercase;
    }
    .month,
    .year {
      font-size: 10px;
    }
  }
  .title {
    a {
      color: white;
    }
  }
}

.tutoral {
  background: url(../../assets/tutoral.png) 20% 40% / contain no-repeat;
}

.hiremepls {
  background: url(../../assets/Hiremepls.png) 20% 40% / contain no-repeat;
}

.techspan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 1em;
  background-color: #0077ff;
  padding: 8px 12px;
  border-radius: 999px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.techdiv {
  display: flex;
  flex-direction: row;
}
